import { PainterConstructor } from '../models/painter-constructor';
import { RandomWalkerPainter } from '../canvas/random-walker-painter';

export class PainterMapper {
  public static map(className: string): PainterConstructor {
    if (RandomWalkerPainter.name === className) {
      return RandomWalkerPainter;
    }
  }
}



