import { ViewChild, ElementRef } from '@angular/core';
import { Painter } from '../models/painter';
import { RandomWalker } from './random-walker';

export class RandomWalkerPainter implements Painter {
  private painterTitle: string = 'Random Walker (perlin noise)';
  private ctx: CanvasRenderingContext2D;
  private randomWalker: RandomWalker;

  constructor(public canvasRef: ElementRef) {
    this.ctx = this.canvasRef.nativeElement.getContext('2d');
    this.randomWalker = new RandomWalker(this.ctx, this.canvasRef.nativeElement.width, this.canvasRef.nativeElement.height);
  }

  public paint() {
    this.randomWalker.step();
    this.randomWalker.display();
  }

  public title(): string {
    return this.painterTitle;
  }
}



