import { Component, OnInit, OnDestroy, ViewChild, ElementRef, NgZone, Input } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

import { Painter } from '../models/painter';
import { PainterConstructor } from '../models/painter-constructor';
import { PainterMapper } from '../utils/painter-mapper';
import { RandomWalkerPainter } from '../canvas/random-walker-painter';

@Component({
  selector: 'app-canvas',
  templateUrl: './canvas.component.html',
  styleUrls: ['./canvas.component.scss']
})
export class CanvasComponent implements OnInit {
  public title: string = 'Painter Component';
  @Input() painterConstructor;
  @ViewChild('canvas') canvasRef: ElementRef;
  private running: boolean;
  private painter: Painter;

  constructor(private ngZone: NgZone, private route: ActivatedRoute) {
  }

  ngOnInit() {
    let painterConstructor: string = this.route.snapshot.params['painterConstructor'];
    this.painter = this.createPainter(PainterMapper.map(painterConstructor));
    this.title = this.painter.title();
    this.running = true;
    this.ngZone.runOutsideAngular(() => {
      requestAnimationFrame(() => this.paint());
    });
  }

  ngOnDestroy() {
    this.running = false;
  }

  private createPainter(pc: PainterConstructor): Painter {
    return new pc(this.canvasRef)
  }

  private paint() {
    // Check that we're still running.
    if (!this.running) {
      return;
    }

    this.painter.paint();

    // Schedule next
    requestAnimationFrame(() => this.paint());
  }

}



