import { NumberHelper } from '../utils/number-helper';
import { Vector } from '../models/vector';

export class RandomWalker {
  public location: Vector;
  public velocity: Vector;
  private noise;

  constructor(private ctx: CanvasRenderingContext2D, private width: number, private height: number) {
    this.noise = window['noise'];
    this.noise.seed(Math.random());
    this.velocity = new Vector(0, 10000);
    this.generateNewLocation();
  }

  public step() {
    this.generateNewLocation();
    this.velocity.add(0.005);
  }

  public display() {
    this.ctx.beginPath();
    this.ctx.ellipse(this.location.x, this.location.y, 1, 1, 45 * Math.PI / 180, 0, 2 * Math.PI);
    this.ctx.stroke();
  }

  private generateNewLocation() {
    this.location = new Vector(
      NumberHelper.map(this.noise.simplex2(this.velocity.x, 0), -1, 1, 0, this.width),
      NumberHelper.map(this.noise.simplex2(this.velocity.y, 0), -1, 1, 0, this.height)
    );
  }
}



